<template>
  <div class="container">
    <vue-headful
      :title="post.title + ' | Writing | Rhys Matthew | Web Developer'"
      description="Rhys Matthew is based in Nottingham, UK. He has a passion for building digital interfaces and creating interactive websites. Rhys loves clean modular code and modern JavaScript frameworks like Vue or React."
      keywords="Writing by Rhys Matthew, writing, UI developer, UX Design, Web Design, Front-End Development, JavaScript dev, Freelance, web engineer, CSS, PHP, Wordpress developer"
      :head="{
        'meta[property=\'og:url\']': {content: 'https://rhys.digital/writing/' + this.$route.params.uid},
        'meta[name=\'twitter:site\']': {content: 'https://rhys.digital/writing/' + this.$route.params.uid},
        'link[rel=\'canonical\']': {href: 'https://rhys.digital/writing/' + this.$route.params.uid}
      }"
    />

    <section class="section pb-0">
      <div class="mw--650">
        <div class="section__header">
          <h1 class="h2 reveal" tabindex="0"><div class="reveal-inner">{{ post.title }}</div></h1>
        </div>
        <div class="section__block">
          <div class="reveal reveal--delay-1">
            <div class="reveal-inner">
              <prismic-rich-text :field="post.body"/>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// Reveal
import reveal from '../components/reveal/_reveal.js'

export default {
  data: function () {
    return {
      post: {
        documentId: '',
        title: '',
        date: '',
        body: []
      }
    }
  },
  created () {
    this.getPostData(this.$route.params.uid)
  },
  mounted () {
    // eslint-disable-next-line
    setTimeout (() => {
      // Dispatch pageChange event
      //
      const pageChange = new CustomEvent('pageChange')
      document.dispatchEvent(pageChange)

      // Initialise reveal effect after content has had
      // enough time to render
      //
      reveal.init()
    }, 1500)
  },
  methods: {
    getPostData: function (uid) {
      // Query to get post content
      this.$prismic.client.getByUID('post', uid)
        .then((document) => {
          if (document) {
            this.post.documentId = document.id
            this.post.title = document.data.title
            this.post.date = document.data.date
            this.post.body = document.data.body
          } else {
            // Return error page
            this.$router.push({ name: 'Page not found' })
          }
        })
    }
  }
}
</script>
